.container {
    top: 0;
    left: 0;
    min-height: 60px;
    width: auto;
    width: 85%;
    margin-left: 220px;
    background-color: #FC4E5C;

}

.wrapper {
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    padding: 10px;
    width: 85%;
    /* border: 1px green solid; */

}

.searchInput {
    width: 70%;
    height: 40px;
    border: none;
    padding-left: 15px;
}

.btn {
    width: 120px;
    height: 40px;
    background-color: #90E04A;
    color: white;
    font-size: 16px;
    border: none;
    font-family: toma_sansregular;
}

.btn:hover {
    background-color: rgb(183, 245, 129);
    color: black;
}

.filter {
    margin: 0 auto;
    color: white;
    font-size: 22px;
    display: flex;
    align-items: center;
}


@media only screen and (max-width:670px) {
    .container {
        width: 100%;
        margin-left: 0;
    }
}
.container-menu {
    top: 0;
    left: 0;
    background-color: black;
    min-height: 100vh;
    height: auto;
    width: auto;
    min-width: 220px;
    float: left;
    overflow: hidden;
}

#sidebar {
    margin-bottom: -101%;
    padding-bottom: 101%;
}

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 60px;
    width: auto;
    font-size: 46px;
    color: white;
    font-family: PhosphatePro-Solid;
}

.ulMenu {
    display: flex;
    flex-direction: column;
    text-align: left;
    min-height: 60px;
    width: 100px;
    color: white;
    margin: 0 auto;
    list-style: none;
    margin-top: 70px;
    line-height: 55px;
    font-size: 18px;
}


@font-face {
    font-family: PhosphatePro-Solid;
    src: url(/movies/static/media/PhosphatePro-Inline.d53c70e9.otf) format("opentype");
}


